export default {
	components: {
	},
	data() {
		return {
			addresstext: '',
			dataLists: this.dataList,
			itemsDatas: this.itemsData,
		};
	},
	props: [
		'disabled',
		'dataList',
		'itemsData',
		'indexNum',
	],
	watch: {
		itemsDatas: {
			handler(a, b) {
				this.itemsDatas = a;
				this.resetAddress();
			},
			deep: true
		}
	},
	created () {
		this.resetAddress();
	},
	mounted() {},
	beforeDestroy () {
		console.log('页面销毁之前缓存数据更新-----------setImgList、setCaiGouList');
	},
	methods: {
		resetAddress() {
			let address = this.itemsDatas.cityStr.split('-');
			this.addresstext = address[0] + address[1] + address[2] + this.itemsDatas.value;
		}
	}
};
